/* || ##FOOTER */

/* Footer Styles */
.footer {
    background-color: #333;
    color: #fff;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer__content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .footer__text {
    font-size: 16px;
    margin: 0;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .footer {
      padding: 15px 0;
    }
  
    .footer__text {
      font-size: 14px;
    }
  }
  