/* Navbar Styles */
.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #333;
  transition: background-color 0.3s ease;
  color: #fff;
  padding: 1.5rem 0;
  margin: 0 0;
}

.navbar__header {
  display: flex;
  justify-content: center;
  width: 100%;
}

.navbar__title {
  font-size: 1.5rem;
}

.navbar__title-link {
  color: #fff;
  text-decoration: none;
}

.navbar__list {
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
}

.navbar__item {
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  transition: background-color 0.3s ease;
  padding: 5px 20px;
}

.navbar__item:hover {
  color: #ffcc00; /* Change the text color on hover */
  transform: scale(1.001); /* Slightly enlarge the item */
  transition: color 0.3s ease, transform 0.3s ease; /* Smooth transitions */
}

.navbar__link {
  color: #fff;
  text-decoration: none;
  display: flex;
  place-items: center;
}

.navbar__icon {
  font-size: 2rem;
}

.navbar__icon-wrapper {
  position: relative;
  display: inline-block;
  display: flex;
  padding-right: 0.5rem;
}

.navbar__favourites-count {
  position: absolute;
  top: -10px;
  right: -2px;
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: bold;
}

.navbar__search-form {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 15px;
  padding: 0;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
}

.navbar__search-fieldset {
  border: none;
  padding: 0; /* Remove padding from fieldset */
  margin: 0; /* Remove margin from fieldset */
  
  background-color: #fff;
  padding-left: 0.5rem;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

.navbar__search-fieldset:focus-within {
  border: 1.5px solid brown; /* Add border to fieldset on focus */
}

.navbar__search-label {
  display: none;
}

.navbar__search-input {
  border: none;
  margin: 0; /* Remove margin from input */
  height: 35px;
  border-radius: 1rem;
  
  /* padding-left: 0.2rem; */
  
}

.navbar__search-input:focus {
  outline: none; /* Add border to input on focus */
}

.navbar__search-form:focus-within {
  border: none; /* Ensure no border on form when input is focused */
}

/* Search Button */
.navbar__search-button {
  background: none;
  background-color:#fff;
  border: none;
  cursor: pointer;
  color: #000;
  transition: color 0.3s ease, transform 0.3s ease;
  padding-right: 1rem;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.search-icon {
  font-size: 21px; /* Adjust the size of the icon as needed */
  color: #000; /* Adjust the color of the icon as needed */
  margin-left: 0.7rem;
}

.navbar__search-button:hover,
.navbar__search-button:focus {
  color: #ffcc00; /* Change color on hover/focus for better UX */
  transform: scale(1.1); /* Slightly enlarge the icon on hover/focus */
}

.navbar__search-button:active {
  transform: scale(0.9); /* Slightly shrink the icon on click for feedback */
}

.navbar__search-button:focus {
  outline: 2px solid #ffcc00; /* Add focus outline for accessibility */
  outline-offset: 2px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .navbar__item {
    margin: 0.5rem 0;
    padding: 5px;
  }
}
